@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.header-nav-wrapper{width:100%;top:0;background-color:#fff;position:-webkit-sticky;position:sticky;z-index:20}.header-bar{width:100%;height:5px;background:linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);background-size:400% 400%;-webkit-animation:headerbar 15s ease infinite;animation:headerbar 15s ease infinite}.header-navbar{display:grid;grid-template:"link . . menu search";grid-template-columns:-webkit-max-content 1fr 1fr -webkit-max-content -webkit-max-content;grid-template-columns:max-content 1fr 1fr max-content max-content;height:50px;line-height:50px;color:#9aa9bb;position:relative}.header-nav{grid-area:menu;margin-right:25px}.header-nav .header-nav-item{display:inline;list-style:none;padding-right:15px}.header-nav .header-nav-item .header-list-icon{padding-right:5px}.header-nav .active-item{color:#3498db}.header-nav-item .header-list-name{font-size:0.9rem;font-weight:400;text-decoration:none;transition:color 0.3s ease-out;cursor:pointer}.header-nav-item a:hover{color:#3498db}.header-menu-toggle .bar{width:25px;height:3px;background-color:#3f3f3f;margin:5px auto;transition:all 0.3s ease-in-out}.header-menu-toggle{grid-area:menu;justify-self:end;margin-right:25px;display:none}.header-menu-toggle:hover{cursor:pointer}#header-mobile-menu.is-active .bar:nth-child(2){opacity:0}#header-mobile-menu.is-active .bar:nth-child(1){transform:translateY(8px) rotate(45deg)}#header-mobile-menu.is-active .bar:nth-child(3){transform:translateY(-8px) rotate(-45deg)}.search-input{grid-area:search;margin-top:10px;width:auto;border:1px solid #9aa9bb;padding:5px;height:36px;border-radius:5px;outline:none;color:#9dbfaf;line-height:36px}.disabled{display:none}::-webkit-input-placeholder{color:#9aa9bb;font-size:14px}::-moz-placeholder{color:#9aa9bb;font-size:14px}:-ms-input-placeholder{color:#9aa9bb;font-size:14px}:-moz-placeholder{color:#9aa9bb;font-size:14px}@-webkit-keyframes headerbar{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes headerbar{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@media only screen and (max-width: 720px){.header-nav-open{overflow:hidden}.header-navbar .header-nav{display:flex;flex-direction:column;position:fixed;top:55px;background-color:#fff;width:100%;height:calc(100vh - 55px);transform:translate(-101%);text-align:justify;overflow:hidden;z-index:10}.header-search{display:flex;flex-direction:column;position:fixed}.header-navbar li{list-style:none}.header-navbar li:first-child{margin-top:50px}.header-navbar li .header-list-name{font-size:1rem}.header-nav-item .header-list-icon{display:none}.header-menu-toggle,.bar{display:block;cursor:pointer;margin-top:13px}.header-mobile-nav{transform:translate(0%) !important}.search-input{margin-right:15px}}

.app{text-align:center}

.main{text-align:center;height:100vh;background-color:#020e18;overflow-y:scroll}

.main-content{margin-bottom:50px}.grid-movie-title{display:grid;grid-template-areas:"movieType . paginate";grid-template-columns:-webkit-max-content 1fr -webkit-max-content;grid-template-columns:max-content 1fr max-content;grid-template-rows:1fr;margin-bottom:30px;font-size:18px;color:#ffffff;width:inherit}.grid-movie-title .movieType{grid-area:movieType;text-align:left;padding:8px 25px}.grid-movie-title .paginate{grid-area:paginate;text-align:right;padding-right:25px}

.slider{overflow:hidden;position:relative;display:flex;flex-direction:column}.slider-image{background-size:cover;background-position:center;background-repeat:no-repeat;height:700px;color:#fff;text-align:center}.slider-nav{align-content:center;display:flex;justify-content:center;margin-top:10px;order:2}.slider-navButton{background:none;border:0;height:20px;opacity:0.5;outline:none !important;position:relative;transition:opacity 0.25s;width:20px;padding:0}.slider-navButton::after{background:#345;border-radius:50%;content:"";display:block;height:6px;left:7px;position:absolute;top:7px;width:6px}.slider-navButton--active{opacity:1}.slider-navButton--active::after{height:12px;left:4px;opacity:1;top:4px;width:12px}.slider-arrows{height:0;left:0;position:absolute;top:calc(50% - 35px);width:100%;z-index:1}.slider-arrow{background:none;border:none;height:40px;opacity:0.5;outline:none;position:absolute;transition:opacity 0.25s;padding:0px;width:21px;outline:none !important;cursor:pointer}.slider-arrow:hover{opacity:1}.slider-arrow::after,.slider-arrow::before{background:#fff;border-radius:3px;content:"";display:block;height:23px;position:absolute;width:5px;left:8px}.slider-arrow::before{top:0}.slider-arrow::after{bottom:0}.slider-arrow--left{opacity:1;top:0;left:10px}.slider-arrow--left::before{transform:rotate(30deg)}.slider-arrow--left::after{transform:rotate(-30deg)}.slider-arrow--right{opacity:1;top:0;right:10px}.slider-arrow--right::before{transform:rotate(-30deg)}.slider-arrow--right::after{transform:rotate(30deg)}.indicators{padding:0;list-style:none;text-align:center}.indicators li{cursor:pointer;display:inline-block;margin:0 10px;width:25px;height:25px;border-radius:50%;border:2px solid blue;background:white}.indicators li.active{background:blue}

.paginate-button{background-color:#dd003f;padding:8px 15px;text-transform:uppercase;color:#ffffff;border:none;border-radius:30px;margin:0 15px;outline:none !important}.disable{cursor:none;pointer-events:none;background:rgba(255,255,255,0.5)}.pageCount{color:#ffffff;margin:0 5px;padding:10px 5px}

.grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));grid-auto-rows:460px;grid-row-gap:5rem;row-gap:5rem;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem;margin:10px 25px;margin-bottom:50px}.grid-cell{position:relative;display:grid;color:#ffffff;box-shadow:rgba(3,8,20,0.1) 0px 0.15rem 0.5rem,rgba(2,8,20,0.1) 0px 0.075rem 0.175rem;height:100%;width:100%;border-radius:4px;transition:all 500ms;background-size:cover;background-position:center;background-repeat:no-repeat}.grid-cell:hover{box-shadow:rgba(3,8,20,0.1) 0px 0.15rem 0.5rem,rgba(2,8,20,0.1) 0px 0.075rem 0.175rem;transform:translateY(-3px) scale(1.1)}.grid-cell:hover .grid-read-more{display:flex;background:rgba(49,56,64,0.1);transition:all 1s ease-out}.grid-read-more{flex-direction:column;justify-content:center;align-items:center;display:none}.grid-read-more .grid-cell-button{background-color:#dd003f;padding:8px 15px;text-transform:uppercase;color:#ffffff;border:none;border-radius:30px;font-weight:bold;outline:none}.grid-read-more .grid-cell-button a{color:#ffffff;text-decoration:none}.grid-detail{position:absolute;bottom:0;padding-left:10px;padding-right:10px;width:348px;text-align:left;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.grid-detail .grid-detail-title{font-size:14px;font-weight:700;text-transform:uppercase;color:#ffffff}.grid-detail .grid-detail-rating{padding-top:4px;display:flex}.grid-detail .grid-detail-rating .grid-vote-average{color:#ffffff;margin-top:-3px}

.star-rating{display:flex}.back-stars{display:flex;color:#bdbdbd;position:relative}.front-stars{display:flex;color:#ffbc0b;position:absolute;overflow:hidden;top:0}

.spinner{margin:100px auto 0;width:100px;text-align:center;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)}.spinner>div{width:18px;height:18px;background-color:#ffffff;border-radius:100%;display:inline-block;animation:bounce 1.4s infinite ease-in-out both;-webkit-animation:bounce 1.4s infinite ease-in-out both}.spinner .bounce1{animation-delay:-0.32s;-webkit-animation-delay:-0.32s}.spinner .bounce2{animation-delay:-0.16s;-webkit-animation-delay:-0.16s}@keyframes bounce{0%,80%,100%{transform:scale(0)}40%{transform:scale(1)}}@-webkit-keyframes bounce{0%,80%,100%{-webkit-transform:scale(0)}40%{-webkit-transform:scale(1)}}

.grid-search-title{display:grid;grid-template-areas:"text1 text2";grid-template-rows:-webkit-max-content -webkit-max-content;grid-template-rows:max-content max-content;grid-template-columns:-webkit-max-content;grid-template-columns:max-content;margin-top:100px;text-align:left;padding-left:25px;font-size:18px}.grid-text1{grid-area:text1;padding-right:8px;color:#9aa9bb}.grid-text2{grid-area:text2;color:#ffffff}

